import uniIcon from '@/components/uni-icon/uni-icon.vue';
import bugBtn from '@/components/uni-bug-btn/uni-bug-btn.vue';
import goodList from '@/components/good-list/good-list.vue';
import {mapState} from 'vuex';
import {getClientUser, turnPage} from '@/common/global.js';
import {ajaxGetGoods} from '@/data/ajax.js';
export default {
	components: {
		uniIcon,
		bugBtn,
		goodList
	},
	data() {
		return {
			scrollTop: 0,
			defGoodPic: require('@/static/img/defgood.png'),
			// 搜索导航信息
			active: {
				index: '',
				// 方向：up,down
				dire: ''
			},
			// 选中的商品
			chooses: [],
			// 商品列表
			goods: []
		};
	},
	watch: {
		// 'car.changeData': {
		// 	'deep': true,
		// 	'handler': function (val) {
		// 		let info = '';
		// 		let id = '';
		// 		let count = '';
		// 		for (let key in val) {
		// 			id = key;
		// 			count = val[key];
		// 		}
		// 		for (let i = 0; i < this.goods.length; i++) {
		// 			if (this.goods[i].id === id && this.goods[i].count !== count) {
		// 				info = this.goods[i];
		// 				break;
		// 			}
		// 		}
		// 		
		// 		if (info !== '' && count !== '') {
		// 			this.$set(info, 'count', count);	
		// 		}
		// 	}
		// }
	},
	computed: {
		// ...mapState(['user', 'car'])
		user () {
			return this.$store.getters.doneUser;
		},
		totalCount () {
			return this.$store.getters.doneCount;
		},
		totalMoney () {
			return this.$store.getters.doneTotal;
		}
	},
	onLoad() {
		let _this = this;
		let result = ajaxGetGoods('', function (data) {
			data = data.result || [];
			// 注入初始数量 0
			data = JSON.stringify(data).replace(/"stock"/g, '"count":0,"stock"');
			_this.goods = JSON.parse(data);
		});
		// 获取微信用户信息
		getClientUser(function (userInfo) {
			_this.$store.commit('setUser', userInfo);
		});
		
		this.EVENTHUB.$on('updateCount', this.utlUpdateGoodCount);
	},
	methods: {
		// 顶部排序导航点击
		clkNav (index) {
			let dire = this.active.dire === 'up' ? 'down' : 'up';
			this.$set(this.active, 'index', index);
			this.$set(this.active, 'dire', dire);
			this.goods.sort(function(a, b){
				let result = false;
				switch (index){
					case 0:
						result = a.stock < b.stock;
						break;
					case 1:
						result = dire === 'up' ? a.price > b.price : a.price < b.price;
						break;
					default:
						result = dire === 'down' ? a.rebate > b.rebate : a.rebate < b.rebate;
						break;
				}
				return result;
			});
		},
		clkImage () {
			turnPage('my');
		},
		clkConfirm () {
			if (this.$store.state.car.data.length === 0) {
				uni.showToast({title: '请先选择商品', icon: 'none', position: 'bottom'});
			} else {
				turnPage('car');
			}
		},
		clkLine (data) {
			turnPage('detail', data);
		},
		// 更新商品数量
		utlUpdateGoodCount (data) {
			let id = data.id;
			let count = data.count;
			for (let i = 0; i < this.goods.length; i++) {
				if (this.goods[i].id === id && this.goods[i].count !== count) {
					data = this.goods[i];
					break;
				}
			}
				
			if (count || count === 0) {
				this.$set(data, 'count', count);	
			}
		}
	}
};
